import React, { Component } from 'react'

export class Result extends Component {
    
    action = (e)=>{
        const {Title,Year} = this.props.result
        this.props.action(e,Title,Year);
    }

    render() {
        const {Title,Year} = this.props.result
        return (
            <div style = {{display:'inline-block',margin:'3px',width:'100%'}}>
                <li>{Title + ' ('+ Year+')  '}
                < button onClick={this.action}> 
                {this.props.nomination?'Nominate':'Remove'}
                </ button>
                </li>
    
            </div>
        )
    }
}

export default Result
