
import {Component} from 'react';
import axios from 'axios';
import SearchBox from './components/SearchBox'
import ResultBox from './components/ResultBox'
import './App.css'

class App extends Component{
  state = {
    searchResult:[],
    searchKey:'',
    nominateTerms:[]
  }

  getNominateTerms = ()=>{
    let nominateTerms = window.localStorage.getItem('nominateTerms')
    if(nominateTerms){
      return JSON.parse(nominateTerms);
    }

    return []
  }

  
  componentDidMount(){

    this.setState({
      nominateTerms:this.getNominateTerms()
    })
  }

  

  search = (event)=>{
    let searchKey = event.target.value;
    this.setState(state=>({
      searchKey:searchKey
    }))

    axios.get("https://www.omdbapi.com/?apikey=580508eb&s="+searchKey)
    .then(res=>{
      if(res.status === 200){
        this.setState({
          searchResult:res.data.Search})
      }
    })
  }

  nominate = (e,title,year)=>{
    for (let i = 0; i < this.state.searchResult.length; i++) {
      let result = this.state.searchResult[i]
      if(result.Title === title&& result.Year === year){
        this.setState({nominateTerms:[...this.state.nominateTerms,result]},()=>{
          window.localStorage.setItem('nominateTerms',JSON.stringify(this.state.nominateTerms))
        });
        
        break;
      }
    }

    this.setState(
      {
        searchResult:this.state.searchResult.filter(result=>{return result.Title!==title&&result.Year!==year})
      })
  }

  remove = (e,title,year)=>{
    this.setState({
      nominateTerms:this.state.nominateTerms.filter(result=>{return result.Title!==title&&result.Year!==year})
    },()=>{
      window.localStorage.setItem('nominateTerms',JSON.stringify(this.state.nominateTerms))
    });
  }




  
  
  render (){
    return (
      <div>
        <header/>
        <SearchBox search = {this.search}/> 

        <div className='container'>
          
          <ResultBox 
          displayText = {'Result for "'+this.state.searchKey+'"'}
          displayTerms = {this.state.searchResult}
          key ={'resultBox'} 
          action = {this.nominate}
          nominateValue = {true}
          />

          <ResultBox 
          displayText = {'Nomination'}
          displayTerms = {this.state.nominateTerms} 
          key = {'nominateBox'}
          action={this.remove}
          nominateValue = {false}/>
        
        </div>
      </div>

    );
    }
}

export default App;
